.pageWrap {
    text-align: center;
    height: 80vh;
    padding: 4em 10em;
    background:linear-gradient(to Left,#5a71ae,#6577a1,#71b0d2);
    .ant-card {
        background-color: #f0f3f632;
    }
    .ant-form-inline .ant-form-item {
        margin-inline-end:unset !important;
    }

    h1{
        color: #f0f3f6;
        text-shadow: 0px 0px 5px #d3b842;
    }
    .buttonstyle{
        background-color: #e8b235;
        border: 2px solid #ebdcb9 ;
        color: #202942;
        transition: all 0.2s ease-in-out ;
        &:hover {
            background-color: #9d720f !important;
            color: #f0f1f2;
        }
    }

    .importationForm {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 2em 0 4em 0;
        input , button{
            padding: 0.4em 2.5em;
            border: none;
            height: 28px;
            width: 100%;
            text-align: center;
        }
    }

    .todo {
        display: flex;
        align-items: center;
        margin: 0.2em 0em;
        transition: all  0.1s ease ;
        p {
            background-color: #f0f3f6;
            font-size: 1.75em;
            padding: 0.25rem 0rem;
            &.todo-text {
                //width: 50%;
            }
            &.todo-date {
                //width: 20%;
            }
            
        }
        button {
            padding: 0.25em 0.5em;
            font-size: 1.75em;
            border: none;
            cursor: pointer;
            &.check{
                background-color: #37977e;
            }
            &.trash{
                background-color: #c0783d;
            }
        }
        &.done {
            opacity: 0.5;
            text-decoration: line-through;
            text-decoration-thickness: 0.2rem;
        }
    }
}

@keyframes scaleUp {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
@keyframes scaleDown {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(0);
    }
}