}
.container .success .story {
  top: 20%;
  left: 30%;
}
.container .success .story .gameOver {
  height: 100px;
  background-image: url(../images/guessNum/gameOverBTN.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
