.pageWrap {
  text-align: center;
  height: 80vh;
  padding: 4em 10em;
  background: linear-gradient(to Left, #5a71ae, #6577a1, #71b0d2);
}
.pageWrap .ant-card {
  background-color: rgba(240, 243, 246, 0.1960784314);
}
.pageWrap .ant-form-inline .ant-form-item {
  margin-inline-end: unset !important;
}
.pageWrap h1 {
  color: #f0f3f6;
  text-shadow: 0px 0px 5px #d3b842;
}
.pageWrap .buttonstyle {
  background-color: #e8b235;
  border: 2px solid #ebdcb9;
  color: #202942;
  transition: all 0.2s ease-in-out;
}
.pageWrap .buttonstyle:hover {
  background-color: #9d720f !important;
  color: #f0f1f2;
}
.pageWrap .importationForm {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2em 0 4em 0;
}
.pageWrap .importationForm input, .pageWrap .importationForm button {
  padding: 0.4em 2.5em;
  border: none;
  height: 28px;
  width: 100%;
  text-align: center;
}
.pageWrap .todo {
  display: flex;
  align-items: center;
  margin: 0.2em 0em;
  transition: all 0.1s ease;
}
.pageWrap .todo p {
  background-color: #f0f3f6;
  font-size: 1.75em;
  padding: 0.25rem 0rem;
}
.pageWrap .todo button {
  padding: 0.25em 0.5em;
  font-size: 1.75em;
  border: none;
  cursor: pointer;
}
.pageWrap .todo button.check {
  background-color: #37977e;
}
.pageWrap .todo button.trash {
  background-color: #c0783d;
}
.pageWrap .todo.done {
  opacity: 0.5;
  text-decoration: line-through;
  text-decoration-thickness: 0.2rem;
}

@keyframes scaleUp {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@keyframes scaleDown {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0);
  }
}
.calendar {
  width: 300px;
  font-family: Arial, sans-serif;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  background-color: #181120;
}
.calendar .calendar_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.calendar .calendar_header button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
}
.calendar .calendar_header .calendar_title {
  font-size: 1.2rem;
  font-weight: bold;
}
.calendar .calendar_body {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
  padding: 10px;
}
.calendar .calendar_body .calendar_cell {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}
.calendar .calendar_body .calendar_cell.selected {
  background-color: #007bff;
  color: #fff;
  border-radius: 50%;
}
.calendar .calendar_body .calendar_cell.header {
  font-weight: bold;
}
.calendar .calendar_body .calendar_cell:hover {
  background-image: radial-gradient(rgba(200, 200, 200, 0.5), rgba(23, 41, 96, 0.5));
  transition: background-image 0.3s;
  animation: glow 1s infinite alternate;
}

@keyframes glow {
  to {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  }
}