// Calendar.scss

.calendar {
    width: 300px;
    font-family: Arial, sans-serif;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    background-color: #181120;
  
    .calendar_header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
      background-color: #007bff;
      color: #fff;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
  
      button {
        background: none;
        border: none;
        color: #fff;
        cursor: pointer;
      }
  
      .calendar_title {
        font-size: 1.2rem;
        font-weight: bold;
      }
    }
  
    .calendar_body {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      gap: 1px;
      padding: 10px;
      //background-color: #f7f7f7;
  
      .calendar_cell {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        color: #fff;
        cursor: pointer;
        transition: background-color 0.3s;
  
        &.empty {
          //background-color: #f7f7f7;
        }
  
        &.selected {
          background-color: #007bff;
          color: #fff;
          border-radius: 50%;
        }
  
        &.header {
          font-weight: bold;
          //background-color: #eee;
        }
        &:hover {
            background-image:radial-gradient(rgba(200,200,200,0.5),rgba(23, 41, 96, 0.5)); // 懸停時的背景顏色
            //box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); // 懸停時的陰影
            transition: background-image 0.3s; // 添加過渡效果
            animation: glow 1s infinite alternate; // 添加動畫
        }
      }
    }
  }

    // 定義發光動畫
    @keyframes glow {
        to {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.6); // 發光效果
        }
      }
  